import React, { useEffect, useState } from 'react'
import QrCamera from './QrCamera'
import LockerController from './LockerController'
import { useParams, Link } from 'react-router-dom'
import { useSelector } from 'react-redux'

export default function QrIndex(props) {
  const [lockerCode, setLockerCode] = useState(undefined)
  const { paramLockerCode } = useParams()
  const user = useSelector((state) => state.userLog.user)

  const callbackfn = (data) => {
    setLockerCode(data)
  }

  const handleCancel = () => {
    setLockerCode(undefined)
  }

  useEffect(async () => {
    try {
      setTimeout(() => callbackfn(paramLockerCode), 1000)
    } catch (e) {
    }
  }, [])

  return (
    <>
      <div className="container mt-5">
        {user ? (
          <div>
            Xin chào, <b>{user.name}</b>
          </div>
        ) : (
          <div>
            <Link to="/balo/login">Đăng nhập</Link> để mở khoá nhanh hơn.
          </div>
        )}
      </div>
      <br />
      <QrCamera parentCallback={callbackfn} />
      <LockerController lockerCode={lockerCode} onCancel={handleCancel} />
    </>
  )
}
