import React, { useState, useEffect } from 'react'
import { Button, Offcanvas } from 'react-bootstrap'
import { useDispatch } from 'react-redux'
import { useCookies } from 'react-cookie'
import lockerApi from '../../api/lockerApi'
import Authorization from './Authorization'
import { useSelector } from 'react-redux'

export default function LockerController(props) {
  const dispatch = useDispatch()
  const [show, setShow] = useState(false)
  const [cookies] = useCookies(['access_token'])
  const [lockerInformation, setLockerInformation] = useState({
    locker_name: '',
    locker_address: '',
  })
  const user = useSelector((state) => state.userLog.user)

  const [lockerStatus, setLockerStatus] = useState([])

  const sendUnlockRequest = async (_lockerCode) => {
    try {
      setLockerStatus([...lockerStatus, 'Gửi lệnh mở cửa'])
      const response = await lockerApi.unlock(_lockerCode, cookies.access_token)
      console.log(response)
    } catch (e) {
      console.log('Mã lỗi', e.response.status)
      if (e.response.status === 401) {
        setLockerStatus([...lockerStatus])
      }
    }
  }

  const getLockerInformation = async (_lockerCode) => {
    try {
      console.log('_lockerCode', _lockerCode)
      setLockerInformation(await lockerApi.getLocker(_lockerCode))
    } catch (e) {
      console.log(e)
    }
  }

  useEffect(() => {
    props.lockerCode && setShow(true)
  }, [props.lockerCode])

  const handleChange = (e) => {}

  const handleShow = (e) => {
    window.navigator.vibrate(200)
    getLockerInformation(props.lockerCode)
    sendUnlockRequest(props.lockerCode)
  }

  const handleClose = (e) => {
    setShow(false)
    setLockerInformation({
      locker_name: '',
      locker_address: '',
    })
    setLockerStatus([])
    props.onCancel()
  }

  return (
    <>
      <style type="text/css">
        {`
    .offcanvas {
     height: auto
    }
    `}
      </style>

      <Offcanvas
        show={show}
        onShow={handleShow}
        backdrop={true}
        placement="bottom"
        onHide={handleClose}
      >
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>Mở khoá bằng QR Code</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <div className="fw-bold">THÔNG TIN THIẾT BỊ</div>
          <div className="">
            <b>Tên thiết bị:</b> {lockerInformation.locker_name}
          </div>
          <br></br>
          {user ? (
            <div>
              <div className="fw-bold">TRẠNG THÁI</div>
              <div className="">
                {lockerStatus.map((status, index) => {
                  return (
                    <div key={index + 1}>
                      {index + 1}. {status}
                    </div>
                  )
                })}
              </div>
            </div>
          ) : (
            <Authorization />
          )}
        </Offcanvas.Body>
      </Offcanvas>
    </>
  )
}
