import React, { useState, useRef } from 'react'
import { QrReader } from 'react-qr-reader'
import { useNavigate } from 'react-router-dom'
import { useTorchLight } from '@blackbox-vision/use-torch-light'

export default function QrCamera(props) {
  const streamRef = useRef(null)
  const [mode, setMode] = useState('environment')
  const [on, toggle] = useTorchLight(streamRef.current)
  const setRef = ({ stream }) => (streamRef.current = stream)
  const handleResult = (result) => {
    if (result?.text) {
      const lockerCodeStartAt = (result?.text).indexOf('get-locker/') + 11
      const lockerCode = (result?.text).substring(lockerCodeStartAt)
      props.parentCallback(lockerCode)
    }
  }
  return (
    <>
      <QrReader
        onLoad={setRef}
        onResult={(result, error) => {
          if (!!result) {
            handleResult(result)
          }

          if (!!error) {
          }
        }}
        constraints={{ facingMode: mode }}
        scanDelay={500}
      />
      <div className="container">
        <div width="90%">
          <div className="d-flex justify-content-between">
            <div className="btn btn-primary" onClick={toggle}>
              <i className="fa-solid fa-lightbulb"></i> {on ? 'ON' : 'OFF'}
            </div>
            <div
              className="btn btn-primary"
              onClick={() => {
                mode == 'environment' ? setMode('user') : setMode('evironment')
              }}
            >
              <i className="fa-solid fa-camera-rotate"></i>
            </div>
          </div>
          <br />
          <div className="">
            <b>Hướng dẫn: </b> Di chuyển camera vào mã QR trên thiết bị để mở
            khoá.
          </div>
        </div>
      </div>
    </>
  )
}
